import axios from "axios";
import { useEffect, useState } from "react";
import { useInitData } from "@vkruglikov/react-telegram-web-app";
import { Chessboard } from "react-chessboard";
import { Chess, Move, Square } from "chess.js";
import Modal from 'react-modal';
import styles from './index.module.css'
import { BoardOrientation, CustomSquareStyles, PromotionPieceOption } from "react-chessboard/dist/chessboard/types";

export type DailyResponse = IDailyData[]

export interface IDailyData {
  puzzleid: string
  pgn: string
  solution: string[]
  themes: string[]
}

export type StatusGame = 'Win' | 'Lose'

export interface SolutionResult {
  puzzleid: string
  user: User
}

export interface User {
  uid: number
  success: boolean
  name: string
  last_name: string
  username: string
}

const ChessGamePage = () => {
  const [dailyData, setDailyData] = useState<IDailyData>();
  const [currentSolutionMovePosition, setCurrentSolutionMovePosition] = useState(0);
  const [isGameOver, setIsGameOver] = useState<StatusGame>();
  const [initDataUnsafe] = useInitData();
  const [orientation, setOrientation] = useState<BoardOrientation>('white');
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [game, setGame] = useState(new Chess());
  const [moveFrom, setMoveFrom] = useState<Square>();
  const [moveTo, setMoveTo] = useState<Square>();
  const [showPromotionDialog, setShowPromotionDialog] = useState(false);
  const [rightClickedSquares, setRightClickedSquares] = useState<CustomSquareStyles>({});
  const [optionSquares, setOptionSquares] = useState<CustomSquareStyles>({});
  
  const user = initDataUnsafe?.user;

  const getNewGameByPgn = (pgn: string) => {
    const newGame = new Chess();
    if (!pgn)
      return newGame;

    const moves = pgn.split(' ');
    setOrientation(moves.length % 2 ? 'black' : 'white');
    moves.forEach((move) => newGame.move(move));
    return newGame;
  }

  const getMoveOptions = (square: Square) => {
    const moves = game.moves({
      square,
      verbose: true,
    });
    if (moves.length === 0) {
      setOptionSquares({});
      return false;
    }

    const newSquares:CustomSquareStyles = {};
    moves.map((move) => {
      newSquares[move.to] = {
        background:
          game.get(move.to) &&
          game.get(move.to).color !== game.get(square).color
            ? "radial-gradient(circle, rgba(0,0,0,.1) 85%, transparent 85%)"
            : "radial-gradient(circle, rgba(0,0,0,.1) 25%, transparent 25%)",
        borderRadius: "50%",
      };
      return move;
    });
    newSquares[square] = {
      background: "rgba(255, 255, 0, 0.4)",
    };
    setOptionSquares(newSquares);
    return true;
  }

  const onSquareClick = (square: Square) =>  {
    setRightClickedSquares({});

    // from square
    if (!moveFrom) {
      const hasMoveOptions = getMoveOptions(square);
      if (hasMoveOptions) setMoveFrom(square);
      return;
    }

    // to square
    if (!moveTo) {
      // check if valid move before showing dialog
      const moves = game.moves({
        square: moveFrom,
        verbose: true,
      });
      const foundMove = moves.find(
        (m) => m.from === moveFrom && m.to === square
      );
      // not a valid move
      if (!foundMove) {
        // check if clicked on new piece
        const hasMoveOptions = getMoveOptions(square);
        // if new piece, setMoveFrom, otherwise clear moveFrom
        setMoveFrom(hasMoveOptions ? square : undefined);
        return;
      }

      // valid move
      setMoveTo(square);

      // if promotion move
      if (
        (foundMove.color === "w" &&
          foundMove.piece === "p" &&
          square[1] === "8") ||
        (foundMove.color === "b" &&
          foundMove.piece === "p" &&
          square[1] === "1")
      ) {
        setShowPromotionDialog(true);
        return;
      }

      // is normal move
      const gameCopy = new Chess(game.fen());
      const move = gameCopy.move({
        from: moveFrom,
        to: square,
        promotion: "q",
      });

      // if invalid, setMoveFrom and getMoveOptions
      if (move === null) {
        const hasMoveOptions = getMoveOptions(square);
        if (hasMoveOptions) setMoveFrom(square);
        return;
      }

      myWithEnemySolutionMove(gameCopy, move);
            
      resetOptionsSquaresWithMove();
    }
  }

  const onPromotionPieceSelect = (piece?:PromotionPieceOption) => {
    // if no piece passed then user has cancelled dialog, don't make move and reset
    if (piece) {
      const gameCopy = new Chess(game.fen());
      const move = gameCopy.move({
        from: moveFrom!,
        to: moveTo!,
        promotion: piece[1].toLowerCase() ?? "q",
      });
      myWithEnemySolutionMove(gameCopy, move);
    }

    resetOptionsSquaresWithMove();
    setShowPromotionDialog(false);
    return true;
  }

  const myWithEnemySolutionMove = (game: Chess, move: Move) => {
    const isCorrectMove = checkSolution(move.from, move.to);
    setGame(game);
      if (isCorrectMove) {
        moveEnemyWithDelayAsync(game, 250);
      }
  }

  const checkSolution = (sourceSquare: Square, targetSquare: Square, piece?: PromotionPieceOption) => {
    const solution = dailyData!.solution;
    const solutionMove =  solution[currentSolutionMovePosition];   
  
    const currentMove = `${sourceSquare}${targetSquare}${piece ?? ''}`;
    if (currentMove !== solutionMove) {
      setIsGameOver(() => 'Lose');
      return false;
    }
    
    if (currentSolutionMovePosition + 2 > solution.length - 1) {
      setIsGameOver(() => 'Win');
    }
    
    return true;
  }

  const moveEnemy = (game: Chess) => {
    const solution = dailyData!.solution;
    console.log(currentSolutionMovePosition);
    if (currentSolutionMovePosition + 1 === solution.length)
      return setGame(game);

    game.move(
      solution[currentSolutionMovePosition + 1]
    );

    setGame(game);
  }

  const resetOptionsSquaresWithMove = () => {
    setMoveFrom(undefined);
    setMoveTo(undefined);
    setOptionSquares({});
  }

  const onSquareRightClick = (square: Square) => {
    const colour = "rgba(0, 0, 255, 0.4)";
    setRightClickedSquares(prev => {
      return {
        ...prev,
        [square]:
        rightClickedSquares[square] &&
        rightClickedSquares[square]?.backgroundColor === colour
          ? undefined
          : { backgroundColor: colour },
      }
    });
  }

  const moveEnemyWithDelayAsync = async (game: Chess, delay: number) => {
    await new Promise((resolve) => {
      setTimeout(() => {resolve(null)}, delay)
    })
    moveEnemy(game);
    setCurrentSolutionMovePosition(prev => prev + 2);
  }

  // useEffect(() => {
  //   if (!isWaitEnemyDelay)
  //     return; 
  //     test();
  // // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, [game, isWaitEnemyDelay])

  useEffect(() => {
      axios.get<DailyResponse>('https://9a80-88-83-221-2.ngrok-free.app/daily', {
        headers: {
          'ngrok-skip-browser-warning': '123',
        }
      })
      .then(response => setDailyData(response.data[0]))
      .catch(console.log);
  }, []);

  useEffect(() => {
    const pgn = dailyData?.pgn ?? '';
    setGame(getNewGameByPgn(pgn));
  }, [dailyData?.pgn]);

  useEffect(() => {
    if (!isGameOver || !dailyData || !user)
      return;

    axios.post('https://9a80-88-83-221-2.ngrok-free.app/sendres', {
      puzzleid: dailyData.puzzleid,
      user: { 
        uid: user.id,
        success: isGameOver === 'Win' ? true : false,
        name: user.first_name,
        last_name: user.last_name,
        username: user.username
      }
    } as SolutionResult, {
      headers: {
        'Content-Type': 'application/json',
        'ngrok-skip-browser-warning': '123',
      }
    })
    .catch(console.log)
  }, [dailyData, isGameOver, user])
  
  useEffect(() => {
    if (isGameOver)
      setIsOpenModal(true);
  }, [isGameOver]);

  return (
    <div className="ChessGame">
      {dailyData &&
        <Chessboard 
        id="ClickToMove"
        boardOrientation={orientation}
        animationDuration={200}
        arePiecesDraggable={false}
        position={game.fen()}
        onSquareClick={onSquareClick}
        onSquareRightClick={onSquareRightClick}
        onPromotionPieceSelect={onPromotionPieceSelect}
        customBoardStyle={{
          borderRadius: "4px",
          boxShadow: "0 2px 10px rgba(0, 0, 0, 0.5)",
        }}
        customSquareStyles={{
          ...optionSquares,
          ...rightClickedSquares,
        }}
        promotionToSquare={moveTo}
        showPromotionDialog={showPromotionDialog}
          // position={fen}
          // boardOrientation={orientation}
          // areArrowsAllowed={false}
          // arePiecesDraggable={!isGameOver}
          // onPieceDrop={handleDrop}
        />
      }
      <Modal 
        overlayClassName={styles.overlay}
        className={styles.container} 
        isOpen={isOpenModal}
        ariaHideApp={false}
        shouldFocusAfterRender={false}
      >
        <div className={styles.content}>
          <div className={styles.modalText}>
            {isGameOver === 'Win' ?
              '  ' :
              '  '
            }
          </div>
        </div>
      </Modal>
    </div>
  )
};

export default ChessGamePage;
