import React, { useEffect } from 'react';
import './App.css';
import { WebAppProvider, useInitData, useWebApp } from '@vkruglikov/react-telegram-web-app';
import useStyles from './hooks/useStyles';
import { AppProvider } from './context';
import ChessGamePage from './components/ChessGamePage';

function App() {
  const WebApp = useWebApp();
  const style = useStyles();
  const [initDataUnsafe, ] = useInitData();

  useEffect(() => {
    if (!initDataUnsafe?.user?.allows_write_to_pm) {
      WebApp.requestWriteAccess();
    }
  }, [initDataUnsafe?.user?.allows_write_to_pm, WebApp])
  
  useEffect(() => {
    WebApp.headerColor = style.bg_color;
    WebApp.backgroundColor = style.bg_color;
    document.body.style.backgroundColor = style.bg_color;
  }, [WebApp, style]);
  
  return (
    <WebAppProvider
      options={{
        smoothButtonsTransition: true,
      }}
    >
      <AppProvider>
        <ChessGamePage />
      </AppProvider>
    </WebAppProvider>
  );
}

export default App;
